import React from 'react'
// import millify from 'millify'
import { Typography, Row, Col, Statistic } from 'antd'
// import { Link } from 'react-router-dom'

import {useGetCryptosQuery} from '../services/cryptoApi'
const {Title} = Typography;

const Homepage = () => {
  const {data, isFetching} = useGetCryptosQuery();
  const globlaState = data?.data?.stats;
  if(isFetching) return 'loading...'
  return (
    <>
      <Title level={2} className="heading">Global Crypto Stats</Title>
      <Row gutter={[32, 32]}>
        <Col span={12}><Statistic title="Total Cryptocurrencies" value={globlaState.total} /></Col>
        <Col span={12}><Statistic title="Total Exchanges" value="3" /></Col>
        <Col span={12}><Statistic title="Total Market Cap:" value="5" /></Col>
        <Col span={12}><Statistic title="Total 24h Volume" value="3" /></Col>
        <Col span={12}><Statistic title="Total Cryptocurrencies" value="1" /></Col>
        <Col span={12}><Statistic title="Total Markets" value="2" /></Col>
      </Row>
    </>
  )
}

export default Homepage
